/* ====================================================== 
   <!-- Helper Classes -->                                 
/* ====================================================== */
/*
 * 5. Element Floating
 * 6. Element Alignment
 * 12. Tip Bubble
 * 14. Position
*/
/*
 ---------------------------
 5. Element Floating
 ---------------------------
 */
.uix-f-l {
    float: right;
}
.uix-f-r {
    float: left;
}
/* 
 ---------------------------
 6. Element Alignment
 ---------------------------
 */
.uix-t-r {
    text-align: left;
}
.uix-t-l {
    text-align: right;
}
/* The breakpoint is the same as bootstrap 5 */
/*
--breakpoint-xs:0;
--breakpoint-sm:576px;
--breakpoint-md:768px;
--breakpoint-lg:992px;
--breakpoint-xl:1200px;
--breakpoint-xxl:1400px;
*/
@media all and (max-width: 576px) {
    .uix-t-r--sm {
        text-align: left;
    }
    .uix-t-l--sm {
        text-align: right;
    }
}
@media all and (max-width: 768px) {
    .uix-t-r--md {
        text-align: left;
    }
    .uix-t-l--md {
        text-align: right;
    }
}
@media all and (max-width: 992px) {
    .uix-t-r--lg {
        text-align: left;
    }
    .uix-t-l--lg {
        text-align: right;
    }
}
@media all and (max-width: 1200px) {
    .uix-t-r--xl {
        text-align: left;
    }
    .uix-t-l--xl {
        text-align: right;
    }
}
@media all and (max-width: 1400px) {
    .uix-t-r--xxl {
        text-align: left;
    }
    .uix-t-l--xxl {
        text-align: right;
    }
}
/*
 ---------------------------
 12. Tip Bubble
 ---------------------------
 */
.uix-bubble {
    right: auto;
    left: 2.3rem;
    margin-right: .5rem;
    margin-left: auto;
}
@media all and (max-width: 768px) {
    .uix-bubble {
        right: auto;
        left: 1rem;
    }
}
/*
 ---------------------------
 14. Position
 ---------------------------
 */
/* Don't use `width: 100%` because it is wrong if the parent has padding. */
.uix-dir--left {
    right: 0 !important;
    left: auto !important;
}
.uix-dir--right {
    right: auto !important;
    left: 0 !important;
}
.uix-dir--top-left {
    right: 0 !important;
    left: auto !important;
}
.uix-dir--top-right {
    right: auto !important;
    left: 0 !important;
}
.uix-dir--bottom-left {
    right: 0 !important;
    left: auto !important;
}
.uix-dir--bottom-right {
    right: auto !important;
    left: 0 !important;
}