/******/
/* base */
/* ====================================================== 
   <!-- Basic -->                  
/* ====================================================== */
body {
  direction: rtl;
  unicode-bidi: embed;
}

caption,
th,
td {
  text-align: right;
}

.wp-block-quote,
blockquote {
  border-right-width: 5px !important;
  border-right-color: #ce3a3e !important;
  border-left-width: 1px !important;
  border-left-color: #f8f8f8 !important;
}

.wp-block-quote::before,
blockquote::before {
  margin-right: auto;
  margin-left: 0.21875rem;
}

pre.uix-precode label {
  right: -5px;
  left: auto;
}

pre.uix-precode code {
  margin: 0 3rem 0 0;
  border-right: 1px solid #d9d9d9;
  border-left: none;
}

/* ====================================================== 
   <!-- Brand -->                         
/* ====================================================== */
.uix-brand {
  float: right;
}

/* ====================================================== 
   <!-- Bullet List -->      
/* ====================================================== */
/*
 * 1. Normal List Styling
 * 2. Normal Numbers Ordered List
 * 3. Custom Circle Icons Ordered List
 * 4. Big Numbers Ordered List
 * 5. Step Timeline By Numbers
*/
/*
 ---------------------------
 1. Normal List Styling
 ---------------------------
 */
ol.uix-list--normal,
ul.uix-list--normal,
.uix-list--normal ul,
.uix-list--normal ol {
  padding-right: 0.875rem;
  padding-left: auto;
}

/*
 ---------------------------
 2. Normal Numbers Ordered List
 ---------------------------
 */
ol.uix-list--numbered,
.uix-list--numbered ol {
  margin-right: 1rem;
  margin-left: auto;
}

ol.uix-list--numbered > li,
.uix-list--numbered ol > li {
  margin-right: .5rem;
  margin-left: auto;
}

ol.uix-list--numbered > li::before,
.uix-list--numbered ol > li::before {
  right: -2.25em;
  left: auto;
  margin-right: auto;
  margin-left: 0.5em;
}

/*
 ---------------------------
 3. Custom Circle Icons Ordered List
 ---------------------------
 */
ol.uix-list--circle,
ul.uix-list--circle,
.uix-list--circle ol,
.uix-list--circle ul {
  margin-right: 1rem;
  margin-left: auto;
}

ol.uix-list--circle > li,
ul.uix-list--circle > li,
.uix-list--circle ol > li,
.uix-list--circle ul > li {
  margin-right: 1rem;
  margin-left: auto;
}

ol.uix-list--circle > li::before,
ul.uix-list--circle > li::before,
.uix-list--circle ol > li::before,
.uix-list--circle ul > li::before {
  right: -1.5em;
  left: auto;
}

/*
 ---------------------------
 4. Big Numbers Ordered List
 ---------------------------
 */
ol.uix-list--numbered-large li,
.uix-list--numbered-large ol li {
  margin-right: 1.2rem;
  margin-left: auto;
  padding-right: 2.5rem;
  padding-left: auto;
}

ol.uix-list--numbered-large li::before,
.uix-list--numbered-large ol li::before {
  margin-right: -3.5rem;
  margin-left: auto;
}

ol.uix-list--numbered-large.uix-list--numbered-bg li,
.uix-list--numbered-large.uix-list--numbered-bg ol li {
  margin-right: 0;
  padding-right: 4.5rem;
}

ol.uix-list--numbered-large.uix-list--numbered-bg li p,
.uix-list--numbered-large.uix-list--numbered-bg ol li p {
  padding-right: auto;
  padding-left: 2rem;
}

ol.uix-list--numbered-large.uix-list--numbered-bg li::before,
.uix-list--numbered-large.uix-list--numbered-bg ol li::before {
  margin-right: -4rem;
}

/*
 ---------------------------
 5. Step Timeline By Numbers
 ---------------------------
 */
.uix-list--numbered-step {
  margin-right: 1rem;
  margin-left: auto;
}

.uix-list--numbered-step li {
  padding: 0 3.75rem 1.5rem 0;
  border-right: 2px solid #333333;
  border-left: none;
}

.uix-list--numbered-step li::before {
  right: -26px;
  left: auto;
}

.uix-list--numbered-step li:last-child {
  border-right: none;
}

@media all and (max-width: 768px) {
  .uix-list--numbered-step li {
    padding-right: 2.5rem;
  }
}

/* ====================================================== 
   <!-- Entry -->      
/* ====================================================== */
/*
 * 4. Main Content
 * 5. Meta Info
*/
/*
 ---------------------------
 4. Main Content
 ---------------------------
 */
.uix-entry__content pre:not(.uix-precode) {
  border-right: 5px solid #ce3a3e;
  border-left: 1px solid #d9d9d9;
}

/*
 ---------------------------
 5. Meta Info
 ---------------------------
 */
ul.uix-entry__meta.uix-entry__meta--wrap li strong {
  padding-right: auto;
  padding-left: 0.4375rem;
}

/* ====================================================== 
   <!-- 页眉区 -->
/* ====================================================== */
.uix-header__container {
  right: 0;
  left: auto;
}

/* ====================================================== 
   <!-- Helper Classes -->                                 
/* ====================================================== */
/*
 * 5. Element Floating
 * 6. Element Alignment
 * 12. Tip Bubble
 * 14. Position
*/
/*
 ---------------------------
 5. Element Floating
 ---------------------------
 */
.uix-f-l {
  float: right;
}

.uix-f-r {
  float: left;
}

/* 
 ---------------------------
 6. Element Alignment
 ---------------------------
 */
.uix-t-r {
  text-align: left;
}

.uix-t-l {
  text-align: right;
}

/* The breakpoint is the same as bootstrap 5 */
/*
--breakpoint-xs:0;
--breakpoint-sm:576px;
--breakpoint-md:768px;
--breakpoint-lg:992px;
--breakpoint-xl:1200px;
--breakpoint-xxl:1400px;
*/
@media all and (max-width: 576px) {
  .uix-t-r--sm {
    text-align: left;
  }
  .uix-t-l--sm {
    text-align: right;
  }
}

@media all and (max-width: 768px) {
  .uix-t-r--md {
    text-align: left;
  }
  .uix-t-l--md {
    text-align: right;
  }
}

@media all and (max-width: 992px) {
  .uix-t-r--lg {
    text-align: left;
  }
  .uix-t-l--lg {
    text-align: right;
  }
}

@media all and (max-width: 1200px) {
  .uix-t-r--xl {
    text-align: left;
  }
  .uix-t-l--xl {
    text-align: right;
  }
}

@media all and (max-width: 1400px) {
  .uix-t-r--xxl {
    text-align: left;
  }
  .uix-t-l--xxl {
    text-align: right;
  }
}

/*
 ---------------------------
 12. Tip Bubble
 ---------------------------
 */
.uix-bubble {
  right: auto;
  left: 2.3rem;
  margin-right: .5rem;
  margin-left: auto;
}

@media all and (max-width: 768px) {
  .uix-bubble {
    right: auto;
    left: 1rem;
  }
}

/*
 ---------------------------
 14. Position
 ---------------------------
 */
/* Don't use `width: 100%` because it is wrong if the parent has padding. */
.uix-dir--left {
  right: 0 !important;
  left: auto !important;
}

.uix-dir--right {
  right: auto !important;
  left: 0 !important;
}

.uix-dir--top-left {
  right: 0 !important;
  left: auto !important;
}

.uix-dir--top-right {
  right: auto !important;
  left: 0 !important;
}

.uix-dir--bottom-left {
  right: 0 !important;
  left: auto !important;
}

.uix-dir--bottom-right {
  right: auto !important;
  left: 0 !important;
}

/* ====================================================== 
   <!-- Basic Layout Styles -->                                      
/* ====================================================== */
/*
 ---------------------------
 4. Uix Grid
 ---------------------------
 */
[class*='uix-core-grid__col-'] {
  float: right;
}

.uix-core-grid [class*='uix-core-grid__col-'] {
  padding-right: auto;
  padding-left: 15px;
}

.uix-core-grid__row .uix-core-grid [class*='uix-core-grid__col-']:last-of-type {
  padding-left: 0;
}

/* ====================================================== 
   <!-- Mobile Menu -->
/* ====================================================== */
/* Mobile Menu Toggle */
.uix-menu-mobile__toggle {
  right: auto;
  left: 30px;
}

.uix-menu-mobile__toggle span {
  right: 12px;
  left: auto;
}

body.js-uix-menu-opened .uix-menu-mobile__toggle span {
  right: 7px;
}

/* Mobile Menu Container */
.uix-menu__container.is-mobile {
  right: 0;
  left: auto;
  transform: translateX(300px);
}

.uix-menu__container.is-mobile li.multi-column span.multi-column-title,
.uix-menu__container.is-mobile ul.uix-menu > li ul > li {
  padding-right: .625rem !important;
}

.uix-menu__container.is-mobile ul li ul li a, .uix-menu__container.is-mobile ul li ul li span {
  padding-right: 0;
  padding-left: auto;
}

.uix-menu__container.is-mobile li:not(.multi-column) ul ul {
  padding-right: .825em;
  padding-left: auto;
}

.uix-menu__container.is-mobile > .uix-menu__inner > .uix-menu__right-box {
  padding-right: .5rem;
  padding-left: auto;
}

.uix-menu__container.is-mobile > .uix-menu__inner > .uix-menu__right-box a {
  margin-right: auto;
  margin-left: .3rem;
}

.uix-menu__container.is-mobile .uix-menu__arrow-mobile {
  right: auto;
  left: 10px;
}

.js-uix-menu-opened .uix-menu__container.is-mobile {
  transform: translateX(0);
}

/* ====================================================== 
   <!-- Navigation -->                                      
/* ====================================================== */
.uix-menu__container:not(.is-mobile) {
  float: left;
  /*
     ---------------------------
     1. Primary Menu
     ---------------------------
     */
  /*
     ---------------------------
     3. Mega/Dropdown Menu
